export default {
	data(){
		return {
			h5Name: "",
			h5Password: "",
			WEIXINName: "",
			WEIXINPassword: "",
			appPLUSName: "",
			appPLUSPassword: "",
			logintype: ''
		}
	},
	onLoad() {
	
	},
	methods:{
		//H5登录注册
		H5Login(){
			uni.request({
				url: `${this.$api}/user/registOrLogin?qq=434714873&username=${this.h5Name}&password=${this.h5Password}`,
				method: 'POST',
				data: {
					username: this.h5Name,
					password: this.h5Password
				},
				success: res => {
					console.log(res);
					if(res.data.status === 500){
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						})
					}else if(res.data.status === 200){
						this.$store.state.user = res.data.data
						// console.log(this.$store.state.user);
						uni.switchTab({
							url: `../myCenter/myCenter`
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//微信登录注册
		WEIXINLogin(){
			uni.request({
				url: `${this.$api}/user/registOrLogin?qq=434714873&username=${this.WEIXINName}&password=${this.WEIXINPassword}`,
				method: 'POST',
				data: {
					username: this.WEIXINName,
					password: this.WEIXINPassword
				},
				success: res => {
					console.log(res.data);
					if(res.data.status === 500){
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						})
					}else if(res.data.status === 200){
						this.$store.state.user = res.data.data
						// console.log(this.$store.state.user);
						uni.switchTab({
							url: `../myCenter/myCenter`
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//app登录注册
		appPlusLogin(){
			uni.request({
				url: `${this.$api}/user/registOrLogin?qq=434714873&username=${this.appPLUSName}&password=${this.appPLUSPassword}`,
				method: 'POST',
				data: {},
				success: res => {
					console.log(res);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//微信第三方登录
		thirdLoginWEI(e){
				let user = e.detail.userInfo
					console.log(e);
					console.log(user);
						// 微信登录
						uni.login({
							provider: 'weixin',
							success: res => {
								// 获得微信登录的授权码
								let code = res.code
								uni.request({
									url: `${this.$api}/stu/mpWXLogin/${code}`,
									method: 'POST',
									header: {
										'qq': '122212489'
									},
									data: {
										avatarUrl: user.avatarUrl,
										nickName: user.nickName,
										whichMP: 1
									},
									success: res => {
										console.log(res);
										if (res.data.status === 200) {
			                               
										}
									}
								})
							}
						})
		},
		thirdLogin(e){
			this.logintype = e.currentTarget.dataset.logintype
			console.log(this.logintype);
				uni.login({
					provider: this.logintype,
					success: res => {
					   console.log(res)
							// 成功以后 获取用户的信息
						uni.getUserInfo({
						   provider: this.logintype,
							success: response => {
							// console.log(JSON.stringify(response));
								let face = ''
								let nickname = ''
							   let openIdOrUid = ''
								let userInfo = response.userInfo
									if (this.logintype === 'weixin') {
									 face = userInfo.avatarUrl
									 nickname = userInfo.nickName
									   openIdOrUid = userInfo.openId
									} else if (this.logintype === 'qq') {
									face = userInfo.figureurl_qq_2
									nickname = userInfo.nickname
									openIdOrUid = userInfo.openId
								} else if (this.logintype === 'sinaweibo') {
									face = userInfo.avatar_large
									nickname = userInfo.nickname
									openIdOrUid = userInfo.id
							}
										// 使用一键登录
								uni.request({
									url: `${this.$api}/appUnionLogin/${this.logintype}?qq=122212489`,
									method: 'POST',
									data: {
										face: face,
										openIdOrUid: openIdOrUid,
										nickname: nickname
									},
									success: res => {
										if (res.data.status === 200) {
											console.log(res.data.data);
											let userInfo = res.data.data
											uni.setStorageSync('user', userInfo)
											uni.switchTab({
												url: '/pages/me/me'
											})
										}
			
									},
										fail: () => {},
										complete: () => {}
									});
								}
							})
							},
				    fail: () => {},
				  complete: () => {}
			});
		}
	},
	computed: {

    }
}
